import React, { useEffect, useState } from "react";
import firebase from "../firebase/firebase";
import Header from "./Header";
import {analytics} from "../firebase/firebase";
import ReactGA from 'react-ga';

const FireStoreData = () => {
  const [todo, setTodo] = useState("");
  const [dataList, setDataList] = useState([]);
  const [currentid, setCurrentid] = useState("");
  const [dataId, setDataId] = useState("")
  const ref = firebase.firestore().collection("data");
  const [btnName,setBtnName] = useState("Save");
  
  const clearData = () => {
    setTodo("");
  }

  useEffect(() => {
      ref.onSnapshot((Snapshot) => {
      const item =[];
      Snapshot.forEach((doc) => {
        const {todo} = doc.data();
        item.push({
          key:doc.id,
          todo
        })
      })
      setDataList(item);
      localStorage.setItem("todoData",JSON.stringify(item));
    });
  
  // catch{
  //   console.warn(`erorororrororor`)
  //     let collection = JSON.parse(localStorage.getItem("todoData"));
  //     setDataList(collection);
  //   }
  }, []);

  useEffect(() => {
    // analytics.logEvent({eventName: "Firestorepage_visited"})
    ReactGA.pageview(window.location.pathname + window.location.search);

  }, []);
 
  useEffect(() => {
    if(currentid == ""){
      setTodo(todo)
    }else{
      setTodo(dataList[dataId].todo)
    }
  }, [dataId,currentid])
  const addOrEdit = () => {
      if(btnName === "Save"){
        ref
        .add({ todo })
        .then((docRef) => {
          // console.log("Document written with ID: ", docRef.id);
        })
        .catch((error) => {
          console.error("Error adding document: ", error);
        });
        clearData();
        ReactGA.event({
          category: 'TODO',
          action: 'Add TODO'
        });
       }
    else if(btnName === "Edit"){
      ref.doc(currentid).update({todo});
      setBtnName("Save");
      clearData();
      ReactGA.event({
        category: 'EDIT TODO',
        action: 'Edit an TODO',
        label: "update Record"
      });
     };
    
  }

  const handleDelete = (key) => {
    ref.doc(key).delete()
    ReactGA.event({
      category: 'TODO',
      action: 'Delete an TODO'
    });
   };
   const handleSubmit = () => {
     addOrEdit();
   }
   
  return (
    <div>
      <div>
        <Header />
      </div>
      <br />
      <div className="mt-9">
        <label>Add Todo :-</label>
        <input
          type="text"
          name="todo"
          value={todo}
          onChange={(e) => setTodo(e.target.value)}
        />
        <button onClick={handleSubmit}>{btnName}</button>
        <hr />
        <div className="col-md-9 mt-5">
          <table className="table table-borderless table-stripped">
            <thead className="thead-light">
              <tr>
                <th>TODO LIST</th>
                <th>Edit</th>
                <th>Delete</th>
              </tr>
            </thead>
            <tbody>
               {dataList.map((v,id) => {
                return (
                  <tr key={id}>
                    <td>{v.todo}</td>
                    <td>
                      {
                        <button
                          className="btn btn-primary"
                          onClick={() => {
                              setCurrentid(v.key);
                              setDataId(id);
                              setBtnName("Edit")
                          }}
                        >
                          Edit
                        </button>
                      }
                    </td>
                    <td>
                      {
                        <button
                          className="btn btn-danger"
                          onClick={() => {
                            handleDelete(v.key)
                          }}
                        >
                          Delete
                        </button>
                      }
                    </td>
                  </tr>
                );
            })}
            </tbody> 
          </table>
        </div>
      </div>
    </div>
  );
};

export default FireStoreData;
